.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: steelblue;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.headerModule {
  font-size: 30px;
  color: lightgray;
  cursor: pointer;
}

.headerModuleId {
  font-size: 35px;
  font-weight: bold;
  color: white;
}
